import * as React from "react";
import "./Login.css";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import "./Login.css";
import { Link } from "react-router-dom";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

export default function Login() {
  const [open, setOpen] = React.useState(false);

  const moveBackToLoginFromSignup = () => {
    setOpen(true);
    setTimeout(function () {
      document.querySelector(".bg-modal-signup").style.display = "none";
    }, 3000);
  };

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
  };
  async function openSignUp() {
    document.querySelector(".bg-modal-signup").style.display = "flex";
  }
  async function closeSignUp() {
    document.querySelector(".bg-modal-signup").style.display = "none";
  }
  return (
    <div>
      <div className="square">
        <p
          style={{
            fontSize: 40,
            fontWeight: "bold",
            fontFamily: "Times New Roman, Times, serif",
          }}
          align="center"
        >
          LOGIN
        </p>
        <input
          id="userNameLogin"
          className="loginInput"
          placeholder="Username"
          type="text"
        ></input>
        <br />
        <br />
        <input
          id="passwordLogin"
          className="loginInput"
          placeholder="Password"
          type="password"
        ></input>
        <br />
        <br />
        <Stack spacing={2.2} direction="row">
          <Button
            variant="contained"
            style={{
              maxWidth: "250px",
              maxHeight: "50px",
              minWidth: "250px",
              minHeight: "30px",
            }}
          >
            Continue
          </Button>
          <Link to="/home">Home</Link>
          <Button
            onClick={openSignUp}
            variant="contained"
            style={{
              maxWidth: "250px",
              maxHeight: "50px",
              minWidth: "250px",
              minHeight: "30px",
            }}
          >
            New User
          </Button>
        </Stack>
      </div>
      <div class="bg-modal-signup">
        <div class="modal-content-signup">
          <p
            style={{
              fontSize: 40,
              fontWeight: "bold",
              fontFamily: "Times New Roman, Times, serif",
            }}
            align="center"
          >
            SIGN UP
          </p>
          <Stack
            style={{ paddingLeft: 20, paddingRight: 20 }}
            spacing={5}
            direction="row"
          >
            <input
              id="nameSignup"
              className="signupInput"
              placeholder="Name"
              type="text"
            ></input>
            <input
              id="usernameSignup"
              className="signupInput"
              placeholder="Username"
              type="text"
            ></input>
          </Stack>
          <br />
          <Stack
            style={{ paddingLeft: 20, paddingRight: 20 }}
            spacing={5}
            direction="row"
          >
            <input
              id="emailSignup"
              className="signupInput"
              placeholder="Email Address"
              type="email"
            ></input>
            <input
              id="numberSignup"
              className="phoneNumber"
              placeholder="Phone No"
              type="number"
            ></input>
          </Stack>
          <br />
          <Stack
            style={{ paddingLeft: 20, paddingRight: 20 }}
            spacing={5}
            direction="row"
          >
            <input
              id="password1Signup"
              className="signupInput"
              placeholder="Password"
              type="password"
            ></input>
            <input
              id="password2Signup"
              className="signupInput"
              placeholder="Confirm Password"
              type="password"
            ></input>
          </Stack>
          <br />
          <Stack
            style={{ paddingLeft: 20, paddingRight: 20 }}
            spacing={2}
            direction="row"
          >
            <Button
              onClick={moveBackToLoginFromSignup}
              variant="contained"
              color="primary"
              style={{
                maxWidth: "322px",
                maxHeight: "50px",
                minWidth: "322px",
                minHeight: "30px",
              }}
            >
              CREATE ACCOUNT
            </Button>
            <Button
              onClick={closeSignUp}
              variant="contained"
              color="error"
              style={{
                maxWidth: "322px",
                maxHeight: "50px",
                minWidth: "322px",
                minHeight: "30px",
              }}
            >
              CANCEL
            </Button>
          </Stack>
          <Snackbar open={open} autoHideDuration={3000} onClose={handleClose}>
            <Alert
              onClose={handleClose}
              severity="success"
              sx={{ width: "100%" }}
            >
              Account Successgfully Created!
            </Alert>
          </Snackbar>
        </div>
      </div>
    </div>
  );
}
